const isNumber = (rule, value, callback) => {
  if (/\s/.test(value)) {
    callback(new Error(''));
  }else{
    if (value != '') {
      if (isNaN(value)) {
        callback(new Error(''));
      } else if (+value > 9999999999) {
        callback(new Error('10'));
      } else if (+value < 0) {
        callback(new Error('0'));
      } else {
        callback();
      }
    }else if (value == '') {
      callback();
    }
  }
};
const isDay = (rule, value, callback) => {
  if (/\s/.test(value)) {
    callback(new Error(''));
  }else{
    if (value != '') {
      if (isNaN(value)) {
        callback(new Error(''));
      } else if (+value > 99999) {
        callback(new Error('5'));
      } else if (+value < 0) {
        callback(new Error('0'));
      } else {
        callback();
      }
    } else if (value == '') {
      callback();
    }
  }
};
const isFloat = (rule, value, callback) => {
  if (value != '') {
    if (!/^-?\d+(\.\d{1,2})?$/.test(+value)) {
      callback(new Error(''));
    } else {
      callback();
    }
  } else if (value == '') {
    callback();
  }
};
const isInt = (rule, value, callback) => {
  if (value != '') {
    // if(!/^([0-9]*[.0-9])$/.test(+value)){
    if (value.indexOf('.') > 0) {
      callback(new Error(''));
    } else {
      callback();
    }
  } else if (value == '') {
    callback();
  }
};
const isAge = (rule, value, callback) => {
  if (value != '') {
    if (value > 150 || value < 0) {
      callback(new Error('0150'));
    } else {
      callback();
    }
  }
};
const isPerson = (rule, value, callback) => {
  if (value != '') {
    if (value == 0) {
      callback(new Error('0'));
    } else {
      callback();
    }
  }
};

export default {
  require: [
    {required: true, message: '', trigger: 'change'},
  ],
  requireAge: [
    {required: true, message: '', trigger: 'change'},
    {validator: isNumber, trigger: 'change'},
    {validator: isInt, trigger: 'change'},
    {validator: isAge, trigger: 'change'},
  ],
  requirePerson: [
    {required: true, message: '', trigger: 'change'},
    {validator: isNumber, trigger: 'change'},
    {validator: isInt, trigger: 'change'},
    {validator: isPerson, trigger: 'change'},
  ],
  money: [
    {validator: isNumber, trigger: 'change'},
    {validator: isFloat, trigger: 'change'},
  ],
  day: [
    {validator: isDay, trigger: 'change'},
    {validator: isInt, trigger: 'change'},
  ],
  requireMoney: [
    {required: true, message: '', trigger: 'change'},
    {validator: isNumber, trigger: 'change'},
    {validator: isFloat, trigger: 'change'},
  ],
  requireDay: [
    {required: true, message: '', trigger: 'change'},
    {validator: isNumber, trigger: 'change'},
    {validator: isInt, trigger: 'change'},
  ],
}



